import React, { Component, lazy, Suspense } from "react";

export default class InputMsisdn extends Component {
    constructor(props) {
        super(props);
        this.state = {
            msisdn: this.props.defaultValue,
            msisdnError: this.props.error ? true : false,
            msisdnErrorMsg: this.props.errorMsg ? this.props.errorMsg : "",
            isLabel: this.props.isLabel ? this.props.isLabel : false,
            isRequired: this.props.isRequired ? this.props.isRequired : false
        };
    }

    componentDidUpdate(prevProps) {
        if (prevProps.error !== this.props.error) {
            this.setState({
                msisdnError: this.props.error ? true : false,
                msisdnErrorMsg: this.props.errorMsg ? this.props.errorMsg : ""
            });
        }
    }

    componentDidMount() {
        if (this.props.defaultValue) {
            this.onChange();
        }
    }

    onChange = () => {
        const msisdn = this.msisdn.value;
        if (
            this.state.isRequired ||
            (!this.state.isRequired && msisdn.length > 0)
        ) {
            const regex = /^(03[0-6])[0-9]{8}$/;
            if (regex.test(msisdn) && msisdn.length == 11) {
                this.setState({
                    msisdnError: false,
                    msisdnErrorMsg: "",
                    msisdn: msisdn
                });
                this.props.onChange(false, msisdn);
            } else if (msisdn.length < 1) {
                this.setState({
                    msisdnError: true,
                    msisdnErrorMsg: "this field can not be left empty.",
                    msisdn: msisdn
                });
                this.props.onChange(true, msisdn);
            } else {
                this.setState({
                    msisdnError: true,
                    msisdnErrorMsg: "Please enter a valid mobile number",
                    msisdn: msisdn
                });
                this.props.onChange(true, msisdn);
            }
        } else {
            this.setState({
                msisdnError: false,
                msisdnErrorMsg: "",
                msisdn: msisdn
            });
            this.props.onChange(false, msisdn);
        }
    };

    render() {
        return (
            <>
                <div className="form-group">
                    {this.state.isLabel ? (
                        <label htmlFor="exampleInputMsisdn">
                            {this.props.label}
                        </label>
                    ) : (
                        ""
                    )}

                    <input
                        type="tel"
                        className={
                            this.state.msisdnError
                                ? "form-control form-control-error"
                                : "form-control"
                        }
                        placeholder={this.props.placeholder}
                        ref={c => (this.msisdn = c)}
                        onChange={this.onChange}
                        value={this.state.msisdn}
                        aria-describedby="msisdnHelp"
                        autoComplete="off"
                        required={this.state.isRequired}
                    />
                    {this.state.msisdnError ? (
                        <small className="form-text text-red">
                            {this.state.msisdnErrorMsg}
                        </small>
                    ) : (
                        ""
                    )}
                </div>
            </>
        );
    }
}
